<template>
  <div>
    <Index-Header></Index-Header>
    <el-container>
      <Index-Aside></Index-Aside>
      <el-main>
          <div class="main-bg">
            <h3 class="order-top"></h3>
            <div class="top-btn">
              <div>
                <label></label>
                <el-select v-model="member" placeholder="" class="select_input">
                    <el-option
                    v-for="item in members"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                </el-select>
              </div>
              <div class="search_btns">
                <el-button type="primary" class="topbtn-btn" @click="searchInfo()"></el-button>
                <el-button type="info" class="topbtn-btn" @click="resetInfo()"></el-button>
              </div>
            </div>
            <div class="order-tabs">
                <el-table ref="singleTable" :data="getData" highlight-current-row border
                style="width:100%;">
                    <el-table-column prop="audit_nick" label=""></el-table-column>
                    <el-table-column prop="amount" label=""></el-table-column>
                    <el-table-column prop="to_wallet_info" label=""></el-table-column>
                    <el-table-column prop="create_time" label=""></el-table-column>
                    <el-table-column prop="audit_status" label="">
                      <template slot-scope="scope">
                          <label v-if="scope.row.audit_status == 0"></label>
                          <label v-if="scope.row.audit_status == 1" class="special2"></label>
                          <label v-if="scope.row.audit_status == 2" class="delete_color"></label>
                      </template>
                    </el-table-column>
                    <el-table-column label="">
                        <template slot-scope="scope">
                            <a class="special" @click="passApply(scope.row)" v-if="scope.row.audit_status != 1"></a>
                            <a class="delete_color" @click="refuseApply(scope.row)" v-if="scope.row.audit_status != 1"></a>
                        </template>
                    </el-table-column>
                </el-table>
                <el-pagination @current-change="handleCurrentChange" class="pagination" :page-size="10" :current-page.sync="currentRow" layout="total, prev, pager, next" :total="totalPage">
                </el-pagination>
                <el-dialog title="" :visible.sync="statusDialogVisible" width="30%">
                    <div class="des-dialog">
                        <p>?</p>
                    </div>
                    <span slot="footer" class="dialog-footer">
                        <el-button @click="statusDialogVisible = false"> </el-button>
                        <el-button type="primary" @click="sureStatus()"> </el-button>
                    </span>
                </el-dialog>
                <el-dialog title="" :visible.sync="statusDialogVisible2" width="30%">
                    <div class="des-dialog">
                        <p>?</p>
                    </div>
                    <span slot="footer" class="dialog-footer">
                        <el-button @click="statusDialogVisible2 = false"> </el-button>
                        <el-button type="primary" @click="sureStatus2()"> </el-button>
                    </span>
                </el-dialog>
            </div>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import Header from "../common/Header";
import Aside from "../common/Aside";
import axios from "axios";
import Vue from "vue";
import commonURL from "../../assets/js/ajax";
import { queryWithdraw , passWithdraw , denyWithdraw } from "@/api/public.js";
import moment from "moment";

export default {
  name: "WithdrawManage",
  data() {
    return {
      loginData: {}, //
      id: "",
      statusDialogVisible: false,
      statusDialogVisible2: false,
      time:[],
      getData: [],
      currentRow: 1,
      totalPage: 0,
      clickdata: {},
      member: 0,
      members: [
        // {
        //   value: "",
        //   label: ""
        // },
        {
          value: 0,
          label: ""
        },
        {
          value: 1,
          label: ""
        },
        {
          value: 2,
          label: ""
        }
      ],
    };
  },
  components: {
    "Index-Header": Header,
    "Index-Aside": Aside
  },
  created() {
    this.$store.state.adminleftnavnum = "2";
  },
  mounted() {
    if(!!localStorage.getItem("loginData")){
      this.loginData = JSON.parse(localStorage.getItem("loginData"));
      this.getAllData();
    }
  },
  methods: {
    handleCurrentChange(val) {
      this.currentRow = val;
      this.getAllData();
    },
    openDetails: function(row, event, column) {
      this.clickdata = row;
    },
    getAllData() {
      let params = {
        uid: this.loginData.id,
        sid: this.loginData.sid,
        audit_status: this.member,
        current_page: this.currentRow,
        page_size: 10
      };
      queryWithdraw(params).then(res => {
        if (res.data.code === 0) {
          this.getData = res.data.data.list;
          this.totalPage = Number(res.data.data.count);
          this.getData.forEach(data => {
            data.amount = Number(data.amount/100).toFixed(2);
            var newDate = new Date();
            newDate.setTime(data.create_time * 1000);
            data.create_time = this.$moment(newDate).format(
            "YYYY-MM-DD HH:mm:ss"
            );
          });
        } else {
          this.$message.error(res.data.msg);
        }
      });
    },
    //
    searchInfo() {
      this.getAllData();
    },
    //
    resetInfo() {
      this.member = "";
      this.currentRow = 1;
      this.getAllData();
    },
    //
    // clickGet(row) {
    //   this.loading = true;
    //   setTimeout(() => {
    //     this.loading = false;
    //     this.getSet(row);
    //   }, 1000);
    // },

    //
    passApply(row) {
      this.id = row.id;
      this.statusDialogVisible = true;
    },
    //
    sureStatus() {
      this.statusDialogVisible = false;
      let params = {
        uid: this.loginData.id,
        sid: this.loginData.sid,
        id: this.id
      };
      passWithdraw(params).then(res => {
        if (res.data.code === 0) {
          this.$message.success(res.data.msg);
          this.getAllData();
        } else {
          this.$message.error(res.data.msg);
        }
      });
    },
    // 
    refuseApply(row) {
      this.id = row.id;
      this.statusDialogVisible2 = true;
    },
    //
    sureStatus2() {
      this.statusDialogVisible2 = false;
      let params = {
        uid: this.loginData.id,
        sid: this.loginData.sid,
        id: this.id
      };
      denyWithdraw(params).then(res => {
        if (res.data.code === 0) {
          this.$message.success(res.data.msg);
          this.getAllData();
        } else {
          this.$message.error(res.data.msg);
        }
      });
    },
  }
};
</script>

<style scoped>

/*  */

.data_top {
  width: 100%;
  background: #E9EEF3;
  padding-bottom: 10px;
  display: flex;
  justify-content: flex-start;
  text-align: left;
}

.data_con {
  width: 22%;
  margin-right: 4%;
  display: flex;
  flex-direction: column;
  background: white;
  padding: 20px 20px 10px 20px;
}

.data_top div:last-child {
  margin-right: 0px;
}

.bottom_data {
  text-align: left;
  color: #585858;
  font-size: 14px;
  margin-top: 20px;
  padding-top: 10px;
  border-top: 1px solid #DEE2E7;
}

.total {
  padding-top: 10px;
  padding-bottom: 20px;
  font-size: 26px;
  font-weight: bold;
}
</style>



