import Vue from 'vue';
import Router from 'vue-router';
import ComLogin from '@/components/page/ComLogin';   //
import WithdrawManage from '@/components/page/WithdrawManage'; //
import UserIdCardManage from '@/components/page/UserIdCardManage'; //
import orderManage from '@/components/page/orderManage'; //vip
import UserListManage from '@/components/page/UserListManage'; //
import platformManage from '@/components/page/platformManage'; //
import platformLogs from '@/components/page/platformLogs'; //
import updatePassword from '@/components/page/updatePassword'; //

Vue.use(Router);

export default new Router({
  // mode:'history',
  routes: [
    {
      path: '/',
      name: 'ComLogin',
      component: ComLogin
    },{
      path: '/ComLogin',
      name: 'ComLogin',
      component: ComLogin
    },{
      path: '/Main/WithdrawManage',
      name: 'WithdrawManage',
      component: WithdrawManage
    },{
      path: '/Main/UserIdCardManage',
      name: 'UserIdCardManage',
      component: UserIdCardManage
    },{
      path: '/Main/orderManage',
      name: 'orderManage',
      component: orderManage
    },{
      path: '/Main/UserListManage',
      name: 'UserListManage',
      component: UserListManage
    },{
      path: '/Main/platformManage',
      name: 'platformManage',
      component: platformManage
    },{
      path: '/Main/platformLogs',
      name: 'platformLogs',
      component: platformLogs
    },{
      path: '/Main/updatePassword',
      name: 'updatePassword',
      component: updatePassword
    },
  ]
});



