<template>
  <div>
    <Index-Header></Index-Header>
    <el-container>
      <Index-Aside></Index-Aside>
      <el-main>
          <div class="main-bg" v-loading="loading" element-loading-text="" element-loading-spinner="el-icon-loading" element-loading-background="rgba(0, 0, 0, 0.8)">
            <h3 class="order-top">VIP</h3>
            <div class="top-btn">
              <div>
                <label></label>
                <input v-model="order_no">
              </div>
              <!-- <div>
                  <label></label>
                  <el-select v-model="pay_status" placeholder="" class="select_input">
                      <el-option
                      v-for="item in members"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                      </el-option>
                  </el-select>
              </div> -->
              <div class="search_btns">
                <el-button type="primary" class="topbtn-btn" @click="searchInfo()"></el-button>
                <el-button type="info" class="topbtn-btn" @click="resetInfo()"></el-button>
              </div>
            </div>
            <div class="order-tabs">
                <el-table ref="singleTable" :data="getData" highlight-current-row border
                style="width:100%;">
                    <el-table-column prop="order_no" label=""></el-table-column>
                    <el-table-column prop="amount" label=""></el-table-column>
                    <el-table-column prop="fee" label=""></el-table-column>
                    <el-table-column prop="arrival_amount" label=""></el-table-column>
                    <el-table-column prop="remark" label=""></el-table-column>
                    <el-table-column prop="vip_item_id" label="vip"></el-table-column>
                    <el-table-column prop="paid_time" label=""></el-table-column>
                    <el-table-column prop="pay_status" label="">
                      <template slot-scope="scope">
                          <label v-if="scope.row.pay_status == 0" class="special"></label>
                          <label v-if="scope.row.pay_status == 1" class="special2"></label>
                      </template>
                    </el-table-column>
                </el-table>
                <el-pagination @current-change="handleCurrentChange" class="pagination" :page-size="10" :current-page.sync="currentRow" layout="total, prev, pager, next" :total="totalPage">
                </el-pagination>
                <el-dialog title="" :visible.sync="sentDialogVisible" width="30%">
                    <div class="des-dialog">
                        <p>?</p>
                    </div>
                    <span slot="footer" class="dialog-footer">
                        <el-button @click="sentDialogVisible = false"> </el-button>
                        <el-button type="primary" @click="sureAllSent()"> </el-button>
                    </span>
                </el-dialog>
            </div>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import Header from "../common/Header";
import Aside from "../common/Aside";
import axios from "axios";
import Vue from "vue";
import commonURL from "../../assets/js/ajax";
import { upgradeToVipOrder } from "@/api/public.js";
import moment from "moment";

export default {
  name: "orderManage",
  data() {
    return {
      loginData: {}, //
      id: "",
      order_no: "",
      time:[],
      getData: [],
      currentRow: 1,
      totalPage: 0,
      clickdata: {},
      editDialogVisible: false,
      sentDialogVisible: false,
      pay_status: "",
      members: [
        {
          value: "",
          label: ""
        },
        {
          value: 0,
          label: ""
        },
        {
          value: 1,
          label: ""
        }
      ],
      loading:false,
      result_list:[],
      tishiDialogVisible: false,
    };
  },
  components: {
    "Index-Header": Header,
    "Index-Aside": Aside
  },
  created() {
    this.$store.state.adminleftnavnum = "1";
  },
  mounted() {
    if(!!localStorage.getItem("loginData")){
      this.loginData = JSON.parse(localStorage.getItem("loginData"));
      this.getAllData();
    }
    var begin = this.$moment(new Date()).format("YYYY-MM-DD");
    var day3 = new Date();
    day3.setTime(day3.getTime()+24*60*60*1000);
    var end = this.$moment(new Date(day3)).format("YYYY-MM-DD");
    this.time.push(begin);
    this.time.push(end);
  },
  methods: {
    handleCurrentChange(val) {
      this.currentRow = val;
      this.getAllData();
    },
    openDetails: function(row, event, column) {
      this.clickdata = row;
    },
    getAllData() {
      if (!this.time) {
        var begin = this.$moment(new Date()).format("YYYY-MM-DD");
        var day3 = new Date();
        day3.setTime(day3.getTime()+24*60*60*1000);
        var end = this.$moment(new Date(day3)).format("YYYY-MM-DD");
      } else {
        begin = this.$moment(new Date(this.time[0])).format("YYYY-MM-DD");
        end = this.$moment(new Date(this.time[1])).format("YYYY-MM-DD");
      }
      let params = {
          // pay_status: this.pay_status,
          uid: this.loginData.id,
          sid: this.loginData.sid,
          order_no: this.order_no,
          current_page: this.currentRow,
          page_size: 10
      };
      upgradeToVipOrder(params).then(res => {
          if (res.data.code === 0) {
            this.getData = res.data.data.list;
            this.totalPage = Number(res.data.data.count);
            this.getData.forEach(data => {
              data.amount = Number(data.amount).toFixed(2);
              data.fee = Number(data.fee).toFixed(2);
              data.arrival_amount = Number(data.arrival_amount).toFixed(2);
              var newDate = new Date();
              newDate.setTime(data.paid_time * 1000);
              data.paid_time = this.$moment(newDate).format(
              "YYYY-MM-DD HH:mm:ss"
              );
            });
          } else {
              this.$message.error(res.data.msg);
          }
      });
    },
    //
    searchInfo() {
      this.getAllData();
    },
    //
    resetInfo() {
      this.order_no = "";
      this.currentRow = 1;
      this.getAllData();
    },
    //
    editData(row) {
      this.editDialogVisible = true;
      this.clickdata = row;
    },
    //
    deleteData(id) {
      this.id = id;
      this.deleteDialogVisible = true;
    }
  }
};
</script>

<style scoped>
</style>



