<template>
  <div>
    <Index-Header></Index-Header>
    <el-container>
      <Index-Aside></Index-Aside>
      <el-main>
          <div class="main-bg">
            <h3 class="order-top"></h3>
            <div class="top-btn">
              <div>
                <label></label>
                <input v-model="mobile">
              </div>
              <div class="search_btns">
                <el-button type="primary" class="topbtn-btn" @click="searchInfo()"></el-button>
                <el-button type="info" class="topbtn-btn" @click="resetInfo()"></el-button>
              </div>
            </div>
            <div class="order-tabs">
                <el-table ref="singleTable" :data="getData" highlight-current-row border
                style="width:100%;">
                    <el-table-column prop="mobile" label=""></el-table-column>
                    <el-table-column prop="nickname" label=""></el-table-column>
                    <el-table-column prop="create_time" label=""></el-table-column>
                    <el-table-column label="">
                      <template slot-scope="scope">
                        <a class="special" @click="showDetail(scope.row)"></a>
                      </template>
                    </el-table-column>
                </el-table>
                <el-pagination @current-change="handleCurrentChange" class="pagination" :page-size="10" :current-page.sync="currentRow" layout="total, prev, pager, next" :total="totalPage">
                </el-pagination>
                <el-dialog title="" :visible.sync="detailDialogVisible" width="30%">
                    <div class="des-dialog">
                      <el-table ref="singleTable" :data="getData2" highlight-current-row border
                style="width:100%;">
                        <el-table-column prop="balance" label=""></el-table-column>
                        <el-table-column prop="username" label=""></el-table-column>
                        <el-table-column prop="idcode" label=""></el-table-column>
                        <el-table-column prop="vip_level" label="vip"></el-table-column>
                      </el-table>
                    </div>
                    <span slot="footer" class="dialog-footer">
                        <el-button @click="detailDialogVisible = false"> </el-button>
                    </span>
                </el-dialog>
            </div>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import Header from "../common/Header";
import Aside from "../common/Aside";
import axios from "axios";
import Vue from "vue";
import commonURL from "../../assets/js/ajax";
import { UserListInfo , UserDetailInfo } from "@/api/public.js";
import moment from "moment";

export default {
  name: "UserListManage",
  data() {
    return {
      loginData: {}, //
      id: "",
      mobile: "",
      time:[],
      getData: [],
      getData2: [],
      currentRow: 1,
      totalPage: 0,
      clickdata: {},
      editDialogVisible: false,
      sentDialogVisible: false,
      pay_status: "",
      members: [
        {
          value: "",
          label: ""
        },
        {
          value: 0,
          label: ""
        },
        {
          value: 1,
          label: ""
        }
      ],
      loading:false,
      result_list:[],
      detailDialogVisible: false,
    };
  },
  components: {
    "Index-Header": Header,
    "Index-Aside": Aside
  },
  created() {
    this.$store.state.adminleftnavnum = "4";
  },
  mounted() {
    if(!!localStorage.getItem("loginData")){
      this.loginData = JSON.parse(localStorage.getItem("loginData"));
      this.getAllData();
    }
  },
  methods: {
    handleCurrentChange(val) {
      this.currentRow = val;
      this.getAllData();
    },
    openDetails: function(row, event, column) {
      this.clickdata = row;
    },
    getAllData() {
      let params = {
        uid: this.loginData.id,
        sid: this.loginData.sid,
        mobile: this.mobile,
        current_page: this.currentRow,
        page_size: 10
      };
      UserListInfo(params).then(res => {
          if (res.data.code === 0) {
            this.getData = res.data.data.list;
            this.totalPage = Number(res.data.data.count);
            this.getData.forEach(data => {
              var newDate = new Date();
              newDate.setTime(data.create_time * 1000);
              data.create_time = this.$moment(newDate).format(
              "YYYY-MM-DD HH:mm:ss"
              );
            });
          } else {
              this.$message.error(res.data.msg);
          }
      });
    },
    //
    searchInfo() {
      this.getAllData();
    },
    //
    resetInfo() {
      this.mobile = "";
      this.currentRow = 1;
      this.getAllData();
    },
    showDetail(row){
      this.getData2 = [];
      this.detailDialogVisible = true;
      let params = {
        uid: this.loginData.id,
        sid: this.loginData.sid,
        user_id: row.id
      };
      UserDetailInfo(params).then(res => {
        if (res.data.code === 0) {
          let obj = res.data.data;
          obj.balance = Number(obj.balance/100).toFixed(2);
          this.getData2.push(obj);
        } else {
            this.$message.error(res.data.msg);
        }
      });
    }
  }
};
</script>

<style scoped>
</style>



